import React from 'react';
import styled from 'styled-components';

const StyledButton = styled.button` // button 스타일
    width: 100%;
    padding: 15px 0;
    margin-top: 15px;
    color: #fff;
    background: #999;
    border-radius: 10px;
    border: none;
    &:hover {
        background: #333;
    }
`;

const Button = (props) => {
    return (
        <StyledButton {...props}/>
    );
};

export default Button;