import React from 'react';
import Slider from "react-slick";
import 'slick-carousel/slick/slick.css';
import './Homesection.scss';
import { RiArrowRightSFill, RiArrowLeftSFill } from 'react-icons/ri';
import styled from 'styled-components';

const Article = styled.div`
    position: relative;
    z-index: 1;
    .slick-arrow {
        position:absolute;
        top: 50%;
        transform: translateY(-50%);
        background: none;
        padding: 10px;
        z-index: 9999;
        width: 100px; // 화살표 사이즈
        height: 100px; // 화살표 사이즈
        color: red;
    }
    .slick-prev {
        left: 100px;
    }
    .slick-next {
        right: 100px;
    }
    .slick-dots {
        position: absolute;
        bottom: 30px;
        left: 50%;
        transform: translate(-50%);
        li {
            display: inline-block;
            &+li { padding-left: 10px;} //li의 바로 다음 li 지목 (& 본인 +다음 (li) 바로다음요소) (css에서는 li+li)
            button {
                width: 30px;
                height: 30px;
                border-radius: 50%;
                background: #fff;
                text-indent: -99999px;
                overflow: hidden;
            }
        }
        .slick-active {
            button{
                background: red;
            }
        }
    }
    .slide {
        position: relative;
        height: 30vw !important; // 화면넓이기준단위 vw, 50vw = 화면넓이의 50%, 우선순위높이기 !important
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        .text {
            position: absolute;
            text-align: center;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            color: #fff;
            font-size: 50px;
        }
        button {
            padding: 10px;
            border-radius: 10px;
            backgorund: #fff;
            &:hover {
                background: red;
                color: lime;
            }
        }
    }
    .slide1 {
        background-image: url('images/img3.jpg');
    }
    .slide2 {
        background-image: url('images/img5.jpg');
    }
    .slide3 {
        background-image: url('images/img7.jpg');
    }
`;


const Homesection = () => {

    const settings = { // slick slide
        autoplay: true,
        autoplaySpeed: 4000,
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        prevArrow:<RiArrowLeftSFill/>,
        nextArrow:<RiArrowRightSFill/>
    };

    return (
        <div className="homesecWrap">
            <Article className="article1">

                <Slider {...settings}> {/* slick slide*/}
                    <div className="slide slide1">
                        <div className="text row">
                        <p>이제, 여행은 가까운 곳에서1</p>
                        <button>가까운 여행지 둘러보기1</button>
                        </div>
                    </div>
                    <div className="slide slide2">
                        <div className="text row">
                        <p>이제, 여행은 가까운 곳에서2</p>
                        <button>가까운 여행지 둘러보기2</button>
                        </div>
                    </div>
                    <div className="slide slide3">
                        <div className="text row">
                        <p>이제, 여행은 가까운 곳에서3</p>
                        <button>가까운 여행지 둘러보기3</button>
                        </div>
                    </div>
                </Slider>
                
            </Article>
            <article className="article2">
                <div className="row">
                    <ul>
                        <li>
                            <a href="#">
                                <img src="https://a0.muscache.com/im/pictures/be4d3ba5-08d7-4afe-95a7-f2da6453886a.jpg?" alt=""/>
                                <div>
                                    <strong>부산</strong>
                                    <p>차로 4.5시간 거리</p>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img src="https://a0.muscache.com/im/pictures/7253e011-7c22-48fd-b75d-d0da35372397.jpg?" alt=""/>
                                <div>
                                    <strong>양양군</strong>
                                    <p>차로 3시간 거리</p>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img src="https://a0.muscache.com/im/pictures/52e8083e-2de2-446d-a860-534eab250541.jpg?" alt=""/>
                                <div>
                                    <strong>속초시</strong>
                                    <p>차로 3시간 거리</p>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img src="https://a0.muscache.com/im/pictures/20e74de0-0eb8-4fca-afb8-b111875acdf5.jpg?" alt=""/>
                                <div>
                                    <strong>전주시</strong>
                                    <p>차로 2.5시간 거리</p>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img src="https://a0.muscache.com/im/pictures/e639b7ab-aee3-48ee-9743-216684a51319.jpg?" alt=""/>
                                <div>
                                    <strong>완주군</strong>
                                    <p>차로 2.5시간 거리</p>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img src="https://a0.muscache.com/im/pictures/ca3737ef-0faf-46ba-b055-b4a2d99e2cea.jpg?" alt=""/>
                                <div>
                                    <strong>강릉시</strong>
                                    <p>차로 3시간 거리</p>
                                </div>
                            </a>
                        </li>
                    </ul>
                    <div className="lifeStyle">
                        <h2>어디에서나, 여행은 살아보는거야!</h2>
                        <ul>
                            <li>
                                <img src="https://a0.muscache.com/im/pictures/a0316ecb-e49b-4b3a-b6b6-c2876b820e8c.jpg?" alt=""/>
                                <strong>집 전체</strong>
                            </li>
                            <li>
                                <img src="https://a0.muscache.com/im/pictures/ff69ac49-64e7-4f4a-ae2b-ee01163d0790.jpg?" alt=""/>
                                <strong>통나무집 및 전원주택</strong>
                            </li>
                            <li>
                                <img src="https://a0.muscache.com/im/pictures/ce6814ba-ed53-4d6e-b8f8-c0bbcf821011.jpg?" alt=""/>
                                <strong>독특한 공간</strong>
                            </li>
                            <li>
                                <img src="https://a0.muscache.com/im/pictures/fbe849a4-841a-41b3-b770-419402a6316f.jpg?" alt=""/>
                                <strong>반려동물 환영</strong>
                            </li>
                        </ul>
                    </div>
                </div>
            </article>
            <article className="article3">
                <div className="row">
                    <div>
                        <img src="images/img8.jpg" alt=""/>
                        <p>상하이 샤오룽바오 만드는 법 배우기</p>
                    </div>
                    <div className="right">
                        <div className="rightFst">
                            <div>
                                <img src="images/img4.jpg" alt=""/>
                                <p>그라피티와 거리예술에 반영된 여성상 탐구</p>
                            </div>
                            <div>
                                <img src="images/img6.jpg" alt=""/>
                                <p>뉴욕 마술사와 백스테이지 구경하기</p>
                            </div>
                        </div>
                        <div>
                            <img src="images/img5.jpg" alt=""/>
                            <p>수제 칵테일을 마시며 젠더에 관해 토론하기</p>
                        </div>
                    </div>
                </div>
            </article>
        </div>
    );
};

export default Homesection;