import React, {useState, useEffect} from 'react';
import axios from 'axios';
import NewsItem from './NewsItem';
import styled from 'styled-components'; // 내부스타일 연결
import { ImSpinner2 } from "react-icons/im";

// Header 부분 Border, loading아이콘
const NewsSectionBlock = styled.div` 
    .row { 
        padding-top: 80px;
        display: flex;
        flex-wrap: wrap;
    }
    .loadWrap {
        font-size: 50px;
        min-height: 300px;
        text-align: center;
        line-height: 600px;
        .loadicon {
            font-size: 100px;
            animation: loadspin infinite 5s linear;
        }
        @keyframes loadspin {
            from {transform: rotate(0deg);}
            to { transform: rotate(2turn);}
        }
    }
`;

const NewsSection = ({category}) => {
    const [articles, setArticles] = useState(null);
    const [loading, setLoading] = useState(false);

    const getNews = async () => {
        const query = category === 'all' ? '' : `&category=${category}`; // 클릭한 카테고리명
        const response = await axios.get(`http://newsapi.org/v2/top-headlines?country=kr${query}&apiKey=1aa9c66a28db4ee49d8c7c0eaa5117da`); // news data 가져오기
        // console.log(response);
        setArticles(response.data.articles);
        setLoading(true);
    }
    useEffect(
        () => {
            getNews()
        },[category]
    )

    if (!loading) {
        return <NewsSectionBlock>
            <div className="loadWrap">
                <ImSpinner2 className="loadicon"/>
            </div>
            </NewsSectionBlock>
    }

    if (!articles) {
        return <NewsSectionBlock></NewsSectionBlock>;
    } else {
        return (
            <NewsSectionBlock>
                <div className="row">
                    {
                        articles.map( (article) => ( <NewsItem key={article.url} article={article}/> ) )
                    }
                </div>
            </NewsSectionBlock>
        );
    }    
};

export default NewsSection;